import React, { Component } from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";

import Menu from "../global/Menu";
import Loading from "../global/Loading";

import { GetProducts } from "../../ducks/products";
import { GetCategories } from "../../ducks/categories";
import { GetCollections } from "../../ducks/collections";
import { fetchHomepage } from "../../ducks/data";

import Footer from "../global/Footer";

let french = require("../../assets/img/brew/french.png");
let moka = require("../../assets/img/brew/moka.png");
let aero = require("../../assets/img/brew/aeropress.png");
let chemex = require("../../assets/img/brew/chemex.png");

require("./BrewGuides.scss");

class BrewGuides extends Component {
  componentDidMount() {
    const { products, categories, collections } = this.props;

    if (!products.fetched) {
      this.props.GetProducts();
    }
  }

  render() {
    let showOnClick = e => {
      console.log(e.target.nextSibling);
      e.target.nextSibling.classList.toggle("dropdowns-hide");
    };
    if (this.props.products !== null) {
      return (
        <div className="container">
          <Menu />
          <div className="container-home">
            <div className="brew">
              <div className="brew-header">Brew Guides</div>
              <div className="brew-methods">
                <a href="" className="brew-methods-item">
                  <img src={french} />
                  <span>French Press</span>
                </a>
                <a href="" className="brew-methods-item">
                  <img src={moka} />
                  <span>Kawiarka</span>
                </a>
                <a href="" className="brew-methods-item">
                  <img src={aero} />
                  <span>Aeropress</span>
                </a>
                <a href="" className="brew-methods-item">
                  <img src={chemex} />
                  <span>Chemex</span>
                </a>
              </div>
              <div className="brew-header">oglne wskazwki</div>
              <div className="brew-basic">
                <ul>
                  <li>
                    Odmierz 26g wieo palonej Kawerki (jeeli zamwie Kawerk
                    w ziarnach  zmiel j w stopniu rednim, lub grubym).
                    Pamitaj, e lo zmielonej kawy moesz dostosowa do
                    osobistych preferencji smakowych.
                  </li>
                  <li>
                    Smak kawy jest czym bardzo subiektywnym - szczeglnie jeli
                    chodzi o moc przyrzdzonego przez nas kubka kawy. Jeeli
                    lubisz mocn kaw, dodaj wiksz ilo zmielonej kawy
                    zachowujc t sam ilo wody. Jeeli lubisz agodn kaw,
                    zmniejsz ilo zmielonej kawy albo zwiksz ilo wody.
                  </li>
                  <li>
                    Jeeli chcesz eksperymentowa z parzeniem kawy, polecamy,
                    aby zmienia tylko jedn zmienn podczas tego procesu.
                    Zmiana gruboci zmielenia, iloci kawy, iloci wody
                    diametralnie wpynie na smak zaparzonej kawy. Zacznij od
                    jednej zmiennej, aby zrozumie rnic miedzy Twoim
                    przepisem, a tym co polecamy.
                  </li>
                  <li>
                    Nawizujc do dugoci parzenia kawy - jeeli masz w planie
                    duo przekroczy czas parzenia, porwnujc do tego co
                    proponujemy, zastanw si nad gruboci zmielenia kawy.
                    Jeeli chcesz szybciej zaparzy kaw, mocniej zmiel kaw.
                    Nasz wsplny cel to idealnie wyparzona kawa, wic prbuj
                    rnych kombinacji i znajd swj idealny smak!
                  </li>
                </ul>
              </div>
              <div className="brew-item">
                <div className="brew-item-left">
                  <img src={french} />
                  <div className="brew-item-left-recipe">
                    <ul>
                      <li>
                        <span>Ilo:</span> 2 kubki
                      </li>
                      <li>
                        <span>Zmielenie:</span> rednie lub grube
                      </li>
                      <li>
                        <span>Proporcje:</span> 1:15
                      </li>
                      <li>
                        <span>Czas parzenia:</span> 8 minuty lub mniej
                      </li>
                    </ul>
                    <div className="arrow" />
                  </div>
                  <ul>
                    <span> Wymaga przedmioty:</span>
                    <li>French Press</li>
                    <li>
                      <a href="/sklep"> 26g wieo palonej Kawerki </a>
                    </li>
                    <li>Mynek, bd zmielona Kawerka</li>
                    <li>Waga</li>
                    <li>Czajnik</li>
                    <li>yeczka</li>
                  </ul>
                </div>
                <div className="brew-item-right">
                  <div className="brew-item-right-header">
                    French Press Brew Guide
                    <span> 500ml</span>
                  </div>
                  <div className="arrow" />
                  <div className="brew-item-right-method">
                    <ul>
                      <span>Instrukcja:</span>
                      <li>
                        Odmierz 26g wieo palonej Kawerki (jeeli zamwie
                        Kawerk w ziarnach  zmiel j w stopniu rednim, lub
                        grubym). Pamitaj, e lo zmielonej kawy moesz
                        dostosowa do osobistych preferencji smakowych.
                      </li>
                      <li>
                        Postaw swj French Press na elektronicznej wadze. Dodaj
                        wieo zmielon Kawerk do French Pressu, wyzeruj wag i
                        przygotuj stoper.
                      </li>
                      <li>
                        Przygotuj wod o temperaturze 90  93 st. Celsjusza.
                        Rozpocznij odmierzanie czasu i wlej 390g wody. French
                        Press wypeni si pian, ktra si wytworzy poprzez gaz
                        uwalniany ze wieo palonej kawy. Wszystko jest ok!
                      </li>
                      <li>
                        Pozwl kawie posta przez ok. 4 minuty. Po upywie 4
                        minut wymieszaj kaw yk, by fusy kawy mogy opa na
                        dno French Pressu. Jeeli wytworzona piana Ci
                        przeszkadza, moesz wzi yk i j wybra. To sprawi,
                        e Twoja kawa bdzie bardziej klarowna.
                      </li>
                      <li>
                        Po zamieszaniu kawy, odstaw j na kolejne 3 minuty - w
                        tym czasie fusy opadn na dno zbiornika.
                      </li>
                      <li>
                        Wstaw grn cz French Pressu do zbiornika z kaw i
                        delikatnie naciskaj. Naciskaj do momentu, a sitko
                        znajdzie si na dnie zbiornika. Pozwl kawie jeszcze
                        troch si ochodzi, a Ty w tym czasie posprztaj,
                        usid i rozkoszuj si smakiem przepysznej Kawerki!
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
              <div className="brew-item">
                <div className="brew-item-left">
                  <img src={moka} />
                  <div className="brew-item-left-recipe">
                    <ul>
                      <li>
                        <span>Ilo:</span> 4 filianki
                      </li>
                      <li>
                        <span>Zmielenie:</span> rednie
                      </li>
                      <li>
                        <span>Proporcje:</span> 1:15
                      </li>
                      <li>
                        <span>Czas parzenia:</span> 2 minuty, lub mniej
                      </li>
                    </ul>
                    <div className="arrow" />
                  </div>
                  <ul>
                    <span> Wymaga przedmioty:</span>
                    <li>Kawiarka (Moka Pot)</li>
                    <li>
                      <a href="/sklep"> 15g wieo palonej Kawerki </a>
                    </li>
                    <li>Mynek lub zmielona Kawerka</li>
                    <li>Waga</li>
                    <li>Czajnik</li>
                    <li>Kubek do kawy</li>
                  </ul>
                </div>
                <div className="brew-item-right">
                  <div className="brew-item-right-header">
                    Kawiarka (Moka Pot) Brew Guide
                  </div>
                  <div className="arrow" />
                  <div className="brew-item-right-method">
                    <ul>
                      <span>Instrukcja:</span>
                      <li>
                        Zwa 15g wieo palonej Kawerki (jeli kupie Kawerk w
                        ziarnach, zmiel j w rednim bd grubym stopniu). 15g
                        powinno wypeni koszyczek znajdujcy si w kawiarce.
                      </li>
                      <li>
                        Zagotuj wod i odstaw czajnik z palnika - pozwoli to
                        unikn metalicznego smaku i zapobiegnie zagotowaniu si
                        kawy.
                      </li>
                      <li>
                        Wlej wod do dolnej czci kawiarki do linii znajdujcej
                        si w rodku naczynia.
                      </li>
                      <li>W koszyczek z kaw do dolnej czci kawiarki.</li>
                      <li>
                        Wsyp 15g kawy do koszyczka i wyrwnaj poziom kawy
                        palcem. Usu ziarna ktre znajduj si na krawdzi
                        kawiarki.
                      </li>
                      <li>
                        Zakr kawiark, ale nie za mocno. W tym celu najlepiej
                        uyj rcznika kuchennego, by przytrzyma doln cze
                        kawiarki w ktrej znajduje si gorca woda.
                      </li>
                      <li>
                        Postaw kawiark na ogniu. Uyj redniej wielkoci
                        pomienia (jeeli posiadasz kuchenk indukcyjn, ustaw
                        j na redni moc).
                      </li>
                      <li>
                        Kiedy kawa zacznie si przedostawa do grnej czci
                        kawiarki usyszysz charakterystyczny odgos fukania.
                        Kiedy podniesiesz klap kawiarki zobaczysz ciemno
                        brzowy strumie kawy. Kiedy strumie kawy zmieni kolor
                        na jasno brzowy, zamknij klap i odstaw kawiark ze
                        rda ciepa.
                      </li>
                      <li>
                        Oblej doln cz kawiarki zimn wod aby zakoczy
                        ekstrakcje kawy w kawiarce. Zapobiegnie to rwnie
                        metalicznemu smakowi kawy.
                      </li>
                      <li>
                        Jak tylko kawa przestanie bulgocze, nalej j do kubkw
                        i rozkoszuj si smakiem Kawerki.
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
              <div className="brew-item">
                <div className="brew-item-left">
                  <img src={aero} />
                  <div className="brew-item-left-recipe">
                    <ul>
                      <li>
                        <span>Ilo:</span> 1 kubek
                      </li>
                      <li>
                        <span>Zmielenie:</span> rednie
                      </li>
                      <li>
                        <span>Proporcje:</span> 1:15
                      </li>
                      <li>
                        <span>Czas parzenia:</span> 2 minuty lub mniej
                      </li>
                    </ul>
                    <div className="arrow" />
                  </div>
                  <ul>
                    <span> Wymaga przedmioty:</span>
                    <li>Aeropress</li>
                    <li>Papierowy Filtr</li>
                    <li>
                      <a href="/sklep"> 16g wieo palonej Kawerki </a>
                    </li>
                    <li>Mynek, bd zmielona Kawerka</li>
                    <li>Waga</li>
                    <li>Czajnik</li>
                    <li>yeczka</li>
                    <li>Kubek do kawy</li>
                  </ul>
                </div>
                <div className="brew-item-right">
                  <div className="brew-item-right-header">
                    Aeropress Brew Guide
                  </div>
                  <div className="arrow" />
                  <div className="brew-item-right-method">
                    <ul>
                      <span>Instrukcja:</span>
                      <li>
                        W filtr do sitka i przepucz go gorc, przegotowan
                        wod.
                      </li>
                      <li>
                        Za sitko do gwnej komory aeropressu i postaw go na
                        kubku w ktrym chcesz zaparzy kaw. Najlepiej, eby
                        Twj kubek i aeropress sta na elektronicznej wadze w
                        celu dokadnego odmierzenia iloci wody do parzenia.
                      </li>
                      <li>
                        Zwa 16g wieo zmielonej Kawerki (jeeli masz cae
                        ziarna Kawerki  zmiel j bezporednio przed samym
                        dodaniem). Wsyp odmierzon Kawerk do gwnej komory
                        aeropressu. Pamitaj, e ilo kawy moesz zmieni w
                        zalenoci od intensywnoci Kawerki i preferencji smaku.
                      </li>
                      <li>
                        Zresetuj wag i przygotuj stoper. Nacinij start i nalej
                        240g wody do gwnego zbiornika. Woda powinna mie 90 
                        93 stopnie Celsjusza i nalanie powinno Ci zaj ok. 20
                        sekund.
                      </li>
                      <li>
                        Od razu po wlaniu wody, zacznij j miesza yk w celu
                        nasycenia wody zmielon kaw. Odstaw j na ok. 1 minut.
                      </li>
                      <li>
                        Po minucie, od kubek z aeropressem na st.
                        Delikatnie w tok z gum na kocu do gwnej komory
                        aeropressu. Upewnij si e zrobie to prawidowo.
                      </li>
                      <li>
                        Kiedy tok jest ju na miejscu, zacznij naciska sta
                        si i tempem. Nie powinno to zaj duej ni 20
                        sekund.
                      </li>
                      <li>
                        Usu sitko i wyjmij tok z aeropressu. Pozwl kawie si
                        troch ochodzi, a Ty w tym czasie posprztaj, usid i
                        rozkoszuj si przepysznym smakiem Kawerki!
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
              <div className="brew-item">
                <div className="brew-item-left">
                  <img src={chemex} />
                  <div className="brew-item-left-recipe">
                    <ul>
                      <li>
                        <span>Ilo:</span> 4 - 6 filianek
                      </li>
                      <li>
                        <span>Zmielenie:</span> rednie lub grube
                      </li>
                      <li>
                        <span>Proporcje:</span> 1:15
                      </li>
                      <li>
                        <span>Czas parzenia:</span> 6 minut, lub mniej
                      </li>
                    </ul>
                    <div className="arrow" />
                  </div>
                  <ul>
                    <span> Wymaga przedmioty:</span>
                    <li>Chemex (6 filianek)</li>
                    <li>Papierowy Filtr</li>
                    <li>
                      <a href="/sklep"> 45g wieo palonej Kawerki </a>
                    </li>
                    <li>Mynek lub zmielona Kawerka</li>
                    <li>Waga</li>
                    <li>Czajnik</li>
                  </ul>
                </div>
                <div className="brew-item-right">
                  <div className="brew-item-right-header">
                    Chemex Brew Guide{" "}
                  </div>
                  <div className="arrow" />
                  <div className="brew-item-right-method">
                    <ul>
                      <span>Instrukcja:</span>
                      <li>
                        Zwa 45g wieo palonej Kawerki (jeli kupie Kawerk w
                        ziarnach, zmiel j w rednim bd grubym stopniu).
                        Pamitaj, e ilo zmielonej kawy moesz dostosowa do
                        osobistych preferencji smakowych.
                      </li>
                      <li>
                        Postaw Chemex na wadze elektronicznej i w filtr.
                        Przepucz filtr gorc wod - dziki temu unikniesz
                        papierowego smaku i od razu podgrzejesz naczynie przed
                        zaparzeniem w nim kawy.
                      </li>
                      <li>
                        Wylej wod, ktra znajduje si w naczyniu i wsyp 45g
                        przygotowanej wczeniej zmielonej kawy. Zresetuj wag i
                        przygotuj stoper.
                      </li>
                      <li>
                        Przygotuj wod o temperaturze ok. 90  93 stopni
                        Celsjusza. Wcz stoper i zacznij nalewa wod do
                        Chemexu. Kiedy wlejesz 75g- 100g wody zatrzymaj si i
                        pozwl kawie przyj wod i uwolni gaz. Staraj si
                        nalewa wod okrnymi ruchami - pozwoli to na
                        jednomierne zaparzenie znajdujcej si w naczyniu kawy.
                      </li>
                      <li>
                        W momencie, gdy kawa ju si nie pieni, zacznij ponowne
                        nalewanie wody - tym razem wlej jej 200g. Pozwl kawie
                        przej przez filtr - odstaw j na ok. 2 minuty.
                      </li>
                      <li>
                        Nalej kolejne 200g wody do Chemexu i odstaw na ok. 3 i
                        p minuty. cznie wlae ju ok. 500g wody.
                      </li>
                      <li>
                        Wlej kolejne 175g wody i ponownie odstaw naczynie na 5
                        minut. cznie ju wlae ok. 675g wody.
                      </li>
                      <li>
                        Pozwl kawie si troch ochodzi, a Ty w tym czasie
                        posprztaj, usid i rozkoszuj si smakiem Kawerki.
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <Footer />
        </div>
      );
    } else {
      return (
        <div>
          <Loading />
        </div>
      );
    }
  }
}

const mapStateToProps = ({ products, categories, collections, data }) => ({
  products,
  categories,
  collections,
  data
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      GetProducts,
      GetCategories,
      GetCollections,
      fetchHomepage
    },
    dispatch
  );

export default connect(mapStateToProps, mapDispatchToProps)(BrewGuides);



