import React, { Component } from "react";

require("./InfoDropdowns.scss");

const InfoDropdowns = props => {
  let showOnClick = e => {
    console.log(e.target.nextSibling);
    e.target.nextSibling.classList.toggle("dropdowns-hide");
  };
  return (
    <div className="dropdowns">
      <div className="dropdowns-open">
        <div className="circle" />
        <div onClick={e => showOnClick(e)} className="dropdowns-text">
          <span>ziarna</span>
          <span>V</span>
        </div>
        <div className="dropdown dropdowns-hide ziarna">
          <ul>
            <li>
              <span>Region:</span> {props.item.selected_item.a1}
            </li>
            <li>
              <span>Farma:</span> {props.item.selected_item.a2}
            </li>
            <li>
              <span>Wysoko uprawy:</span> {props.item.selected_item.a3}
            </li>
            <li>
              <span>Metoda suszenia:</span> {props.item.selected_item.a4}
            </li>
          </ul>
        </div>
      </div>
      <div className="dropdowns-open">
        <div className="circle" />
        <div onClick={e => showOnClick(e)} className="dropdowns-text">
          <span>kupujesz - pomagasz</span>
          <span>V</span>
        </div>
        <div className="dropdown dropdowns-hide">
         Kupujc kaw na naszej stronie internetowej wspomagasz Fundacj Siepomaga.
         <p>Za kade sprzedane 250g kawy, przeznaczamy 1z na rzecz Fundacji Siepomaga. Wicej informacji moesz znale na <a href="/pomagaj-z-kawerka" className="button button-normal">pomagaj z kawerk</a></p>
        </div>
      </div>
      <div className="dropdowns-open">
        <div className="circle" />
        <div onClick={e => showOnClick(e)} className="dropdowns-text">
          <span>brew tips</span>
          <span>V</span>
        </div>
        <div className="dropdown dropdowns-hide">
          Najlepiej zaparzy w: Chemex, Aeropress, Kawiarka i French Press.
          <p> Sprawd nasz dzia <a className="button button-normal" href="/brew-guides">brew guides</a>, aby dowiedzie si jak prawidowo zaparzy wieo palon kawerk! </p>

        </div>
      </div>
      <div className="dropdowns-open">
        <div className="circle" />
        <div onClick={e => showOnClick(e)} className="dropdowns-text">
          <span>kawerka w twoim biurze</span>
          <span>V</span>
        </div>
        <div className="dropdown dropdowns-hide">
          Posiadamy specjaln ofert dla biur i miejsc pracy. 
          <p>ju dzisiaj moesz otrzyma 10% od pierwszego zamwienia w Twoim biurze.</p>
          <p>Wiecej informacji na <a className="button button-normal" href="/oferta-dla-firm">oferta dla firm</a></p>
        </div>
        <div className="circle" />
      </div>
    </div>
  );
};

export default InfoDropdowns;



