import React, { Component } from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";

import Menu from "../global/Menu";
import Loading from "../global/Loading";

import { GetProducts } from "../../ducks/products";
import { GetCategories } from "../../ducks/categories";
import { GetCollections } from "../../ducks/collections";
import { fetchHomepage } from "../../ducks/data";

import Footer from "../global/Footer";

require("./OfertaBiurowa.scss");

class OfertaBiurowa extends Component {
  componentDidMount() {
    const { products, categories, collections } = this.props;

    if (!products.fetched) {
      this.props.GetProducts();
    }
  }

  render() {
    let showOnClick = e => {
      console.log(e.target.nextSibling);
      e.target.nextSibling.classList.toggle("dropdowns-hide");
    };
    if (this.props.products !== null) {
      return (
        <div className="container">
          <Menu />
          <div className="container-rest">
            <div className="oferta-biurowa">
              <div className="oferta-biurowa-header">Oferta dla firm</div>
              <div className="dropdowns">
                <div className="dropdowns-open">
                  <div className="circle" />
                  <div onClick={e => showOnClick(e)} className="dropdowns-text">
                    <span>Kawa w Twoim biurze</span>
                    <span>V</span>
                  </div>
                  <div className="dropdown dropdowns-hide">
                    <p>
                      {" "}
                      <strong>wietny pomys!</strong> Kawerka Coffee Company
                      wierzy e dobra kawa poprawia komfort pracy i pozytywnie
                      wpywa na produktywo. Specjalnie przygotowana oferta kaw
                      Single Origin pomoe zmieni Twoje biuro nie do poznania.
                    </p>
                    <p>
                      Nie jeste osob decyzyjn jeeli chodzi o kaw w Twoim
                      biurze? <strong>Nie ma problemu.</strong> Skontaktuj si z
                      nami, a my wylemy Ci tester do Twojego biura.
                    </p>
                    <p>
                      Jeeli Twoje biuro nawie z nami wspprace, otrzymasz
                      10% wartoci pierwszego zamwienia!
                    </p>
                    <a href="" className="button button-red">
                      Wylij email
                    </a>
                    <a href="" className="button button-red">
                      Zadzwo
                    </a>
                  </div>
                </div>
                <div className="dropdowns-open">
                  <div className="circle" />
                  <div onClick={e => showOnClick(e)} className="dropdowns-text">
                    <span>Kawa w Twojej restauracji</span>
                    <span>V</span>
                  </div>
                  <div className="dropdown dropdowns-hide">
                    <p>
                      <strong>
                        Kawerka posiada szerok ofert kaw Single Origin.
                      </strong>{" "}
                      Pozwl swoim klientom wybra pochodzenie kawy i zaskocz
                      ich nietuzinkowym smakiem espresso.
                    </p>
                    <p>
                      Jeeli zdecydujesz si na wspprace z nami, poza
                      przepyszn kaw,{" "}
                      <strong>
                        przeprowadzimy specjalny kurs, ktry nauczy Twoich
                        pracownikw jak najlepiej przyda kawy Single Origin.
                      </strong>
                    </p>
                    <p>
                      Skontaktuj si z nami jeeli chcesz dowiedzie si wicej!
                    </p>
                    <a href="" className="button button-red">
                      Wylij email
                    </a>
                    <a href="" className="button button-red">
                      Zadzwo
                    </a>
                  </div>
                </div>
                <div className="dropdowns-open">
                  <div className="circle" />
                  <div onClick={e => showOnClick(e)} className="dropdowns-text">
                    <span>Kawa w Twojej kawiarni</span>
                    <span>V</span>
                  </div>
                  <div className="dropdown dropdowns-hide">
                    <p>
                     Jeste wacicielem kawiarni? Na pewno znasz i doceniasz smak kaw Single Origin. Z nami moesz by pewien e kade zamwienie jest <strong>wieo palone i przygotowane specjalnie dla Ciebie.</strong>
                    </p>
                    <p>
                        Wiesz e kade ziarne mog by wypalone w rnym stopniu. Napisz do nas, a <strong>przygotujemy Twojej kawiarni specjalny profil kawy,</strong> ktry bdzie odpowiada Twoim wymaganiom.
                    </p>
                    <p>
                      Odezwij si do nas, a my zaopatrzymy Ci w najlepszej jakoci kaw.
                    </p>
                    <a href="" className="button button-red">
                      Wylij email
                    </a>
                    <a href="" className="button button-red">
                      Zadzwo
                    </a>
                  </div>
                  <div className="circle" />
                </div>
              </div>
              <div className="info-siepomaga">Za kade sprzedane 1000g kawy w ofercie b2b, przeznaczamy 1z na rzecz Fundacji Siepomaga. Kupujc - Pomagasz.</div>
            </div>
          </div>
          <Footer />
        </div>
      );
    } else {
      return (
        <div>
          <Loading />
        </div>
      );
    }
  }
}

const mapStateToProps = ({ products, categories, collections, data }) => ({
  products,
  categories,
  collections,
  data
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      GetProducts,
      GetCategories,
      GetCollections,
      fetchHomepage
    },
    dispatch
  );

export default connect(mapStateToProps, mapDispatchToProps)(OfertaBiurowa);



